tyle-al-disp {\n                font-size: 8px;\n                margin-top: -3px;\n              }\n            }\n          }\n\n          .thamiris__alerts__body-items-item-circle-style:hover {\n            background-color: $--color-primary-light-2;\n          }\n        }\n\n        .thamiris__alerts__body-items-item-message {\n          color:  $--color-black;\n          font-size: 12px;\n          margin: 8px 20px 0px 20px;\n          text-align: center;\n          font-weight: 600;\n          word-break: break-word;\n        }\n\n        .thamiris__alerts__body-items-item-message-line-through {\n          font-size: 12px;\n          margin: 8px 20px 0px 20px;\n          text-align: center;\n          font-weight: 600;\n          text-decoration: line-through;\n          color: $--color-gray;\n          word-break: break-word;\n        }\n      }\n\n    }\n  }\n\n  .thamiris__alerts__confirm {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    .thamiris__alerts__confirm-button {\n      padding: 6px 30px;\n      color: $--color-gray;\n    }\n  }\n}\n\n@media (min-width: 1200px) {\n  .thamiris__alerts__shadow {\n    margin-bottom: 30px;\n  }\n}\n\n","/* Colors\n-------------------------- */\n\n$--color-white: #fff !default;\n$--color-black: #424242 !default;\n$--color-gray: #979797 !default;\n$--color-gray-light-1: mix($--color-white, $--color-gray, 10%) !default;\n$--color-light-gray: #f3f4f5 !default;\n$--color-whatsapp: #075E54 !default;\n\n$--pj-color-darker-blue: #0A0022 !default;\n$--pj-color-dark-blue: #280BA0 !default;\n$--pj-color-blue: #0756e4 !default;\n$--pj-color-light-blue: #018EF0 !default;\n$--pj-color-lighter-blue: #30A9FB !default;\n$--pj-color-white-blue: #F0F9FE !default;\n$--pj-color-disabled-blue: #9ad2f9 !default;\n\n$-pg-color-overlay: #19415F !default;\n\n$--pj-color-red: #CC4148 !default;\n$--pj-color-light-red: #EC767C !default;\n$--pj-color-disable-red: #FFF2F3 !default;\n\n$--color-primary: $--pj-colo